* {
  margin: 0;
  padding: 0;
}

.body-start {
  position: absolute;
  text-align: center;
  font-family: "Myriad Pro";
  color: white;
  display: block;
  width: 100%;
  height: 80vh;
  display: flex;
  z-index: -1;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

section {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.main ul {
  margin-bottom: 1.25rem;
}

.main ul.no-bullet {
  list-style: none;
}

.inline-list {
  margin: 0 auto 1.0625rem auto;
  margin-left: -1.375rem;
  margin-right: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
}
.inline-list > li {
  list-style: none;
  float: left;
  margin-left: 1.375rem;
  display: block;
}
.inline-list > li > * {
  display: block;
}

.main {
  background-color: #85c6db;
  height: 100%;
  -webkit-animation: bg-color-anim 25s linear infinite;
  -moz-animation: bg-color-anim 25s linear infinite;
  -o-animation: bg-color-anim 25s linear infinite;
  animation: bg-color-anim 25s linear infinite;
}

.absolute {
  position: absolute;
}

.cloud_left {
  width: 150px;
  height: 50px;
  overflow: hidden;
  top: 60px;
  left: 60px;
  z-index: 999;
}

.cloud {
  background: white;
  border-radius: 50%;
  display: block;
}

.cloud_left ul,
.land_tree ul {
  margin: 0px;
}

.cloud_left li:nth-child(1) {
  width: 50px;
  height: 50px;
  margin: 30px 0px 0px 0px;
}

.cloud_left li:nth-child(2) {
  width: 70px;
  height: 70px;
  margin: 0px 0px 0px -10px;
}

.cloud_left li:nth-child(3) {
  width: 40px;
  height: 40px;
  margin: 23px 0px 0px -7px;
}

.cloud_right {
  width: 150px;
  height: 50px;
  overflow: hidden;
  top: 100px;
  right: 240px;
  z-index: 2;
}

.cloud_right ul,
.land_tree ul {
  margin: 0px;
}

.cloud_right li:nth-child(1) {
  width: 40px;
  height: 40px;
  margin: 36px 0px 0px 1px;
}

.cloud_right li:nth-child(2) {
  width: 70px;
  height: 70px;
  margin: 0px 0px 0px -11px;
}

.cloud_right li:nth-child(3) {
  width: 50px;
  height: 50px;
  margin: 31px 0px 0px -13px;
}

.sea {
  width: 100%;
  height: 30px;
  background: #2a94d6;
  bottom: 7px;
}

.sea:after {
  content: "";
  position: absolute;
  height: 12px;
  background: #1d7fbb;
  bottom: -10px;
  width: 100%;
  left: 0px;
}

.sea span {
  background-color: #8bd9f5;
  position: absolute;
}

.wave1 {
  width: 100px;
  height: 3px;
  left: 420px;
  top: 10px;
}

.wave2 {
  width: 100px;
  height: 3px;
  left: 433px;
  top: 13px;
}

.wave3 {
  width: 50px;
  height: 3px;
  right: 460px;
  top: 15px;
}

.wave4 {
  width: 50px;
  height: 3px;
  right: 467px;
  top: 18px;
}

.landscape {
  bottom: 15px;
  width: 400px;
  height: 170px;
  z-index: 3;
}

.max_right {
  right: 0px;
}

.max_right .grass {
  left: 20px;
}

.max_right .grass:after {
  right: 0px;
  bottom: 40px;
}

.max_right .land_tree {
  bottom: 40px;
  left: 30px;
}

.grass {
  width: 100%;
  height: 40px;
  left: -20px;
  border-radius: 50px;
  position: absolute;
  background: #126c48;
  bottom: 0px;
  display: block;
  z-index: 1;
}

.grass:after {
  position: absolute;
  content: "";
  width: 50%;
  height: 40px;
  border-radius: 50px;
  background: #219065;
  bottom: 36px;
}

.leftt-gras {
  right: 40px !important;
}

.land_tree {
  width: auto;
  height: 50px;
  display: block;
  position: absolute;
  bottom: 40px;
  overflow: hidden;
}

.land_tree li {
  border-radius: 50%;
}

.land_tree li:nth-child(1) {
  width: 40px;
  height: 40px;
  margin: 30px 0px 0px 0px;
  background: #219065;
}

.land_tree li:nth-child(2) {
  width: 60px;
  height: 60px;
  margin: 0px 0px 0px -10px;
  background: #126c48;
}

.land_tree li:nth-child(3) {
  width: 50px;
  height: 50px;
  margin: 23px 0px 0px -7px;
  background: #219065;
}

.tree1 {
  background: #21904b;
  border-radius: 50%;
  width: 65px;
  height: 65px;
  border-right: 5px solid #157c3c;
  border-bottom: 5px solid #157c3c;
  z-index: -1;
  right: 100px;
  bottom: 120px;
}

.tree1:after {
  position: absolute;
  content: "";
  width: 2px;
  height: 50px;
  background: #fff;
  margin: 30px 0px 0px 3px;
  top: 35px;
}

.tree2 {
  background: #26a76f;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border-right: 5px solid #17955e;
  border-bottom: 5px solid #17955e;
  z-index: -1;
  right: 150px;
  bottom: 80px;
}

.tree2:after {
  position: absolute;
  content: "";
  width: 2px;
  height: 50px;
  background: #fff;
  margin: 23px 10px 0px 0px;
  top: 27px;
}

.boat {
  bottom: 15px;
  z-index: 1;
  -webkit-animation: boatanimate 30s linear infinite;
  -moz-animation: boatanimate 30s linear infinite;
  -o-animation: boatanimate 30s linear infinite;
  animation: boatanimate 30s linear infinite;
}

.boat-body {
  width: 100px;
  height: 20px;
  background: #e25c45;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 20px;
}

.white-body {
  width: 65px;
  height: 16px;
  background: #fff;
  margin: 0px 13px;
  border-top-left-radius: 20px;
}

.windows {
  margin: 0px 12px !important;
}

.boat .circle {
  background: #a18e6e;
  width: 7px;
  height: 7px;
  margin: 5px 3px;
  border-radius: 50%;
}

.smokestack {
  width: 10px;
  height: 17px;
  background: #a18e6e;
  bottom: 30px;
  margin-left: 45px;
}

.plane {
  top: 30%;
  left: 80px;
  -webkit-animation: planeanimate 30s linear infinite;
  -moz-animation: planeanimate 30s linear infinite;
  -o-animation: planeanimate 30s linear infinite;
  animation: planeanimate 30s linear infinite;
}

.plane-body {
  width: 90px;
  height: 15px;
  background: #fff;
  border-bottom: 4px solid #ececec;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.plane .circle {
  width: 5px;
  height: 5px;
  background: #e25c45;
  border-radius: 50%;
  margin: 4px 5px 0 0;
  float: right;
}

.wing1 {
  background: #e25c45;
  width: 15px;
  height: 20px;
  float: right;
  bottom: 3px;
  position: absolute;
  right: 35px;
}

.wing1:after {
  position: absolute;
  content: "";
  background: #0c3545;
  width: 7px;
  height: 28px;
  right: -3px;
  -webkit-transform: rotate(14deg);
  -moz-transform: rotate(14deg);
  -o-transform: rotate(14deg);
  transform: rotate(14deg);
  top: 3px;
}

.flipwing {
  -webkit-transform: scaleY(-1);
  -moz-transform: scaleY(-1);
  -o-transform: scaleY(-1);
  transform: scaleY(-1);
  bottom: 35px;
  height: 16px;
  width: 14px;
}

.teal {
  background: #f6d160;
  width: 16px;
  height: 23px;
  bottom: 35px;
}

.teal:after {
  position: absolute;
  content: "";
  background: #0c3545;
  width: 7px;
  height: 28px;
  right: -3px;
  -webkit-transform: rotate(-16deg);
  -moz-transform: rotate(-16deg);
  -o-transform: rotate(-16deg);
  transform: rotate(-16deg);
  top: -7px;
}

.sun {
  width: 48px;
  height: 48px;
  background: #fab92d;
  border-radius: 50%;
  border-bottom: 5px solid #eaab23;
  border-right: 5px solid #eaab23;
  right: 352px;
  top: 90px;
  z-index: 1;
  -webkit-animation: night 25s linear infinite;
  -moz-animation: night 25s linear infinite;
  -o-animation: night 25s linear infinite;
  animation: night 25s linear infinite;
}

.main h1 {
  margin-top: 143px;
  margin-left: 110px;
  color: #fff;
  font-size: 30px;
}

.btn-round {
  padding: 5px 16px;
  border: 1px solid #0a2b38;
  border-radius: 50px;
  transition: 0.2s ease;
}

.contact-btn {
  color: #92adb8;
  font-size: 17px;
  top: 50%;
  right: 45%;
  position: absolute;
  z-index: 99;
  text-decoration: none;
}

.contact-btn:hover {
  background: #219065;
  color: white;
}

.relative {
  position: relative;
}

.fume {
  margin: 0px;
}

.fume li {
  background-color: white;
  border-radius: 50%;
}

.fume1 {
  width: 7px;
  height: 7px;
  margin-left: 48px;
  -webkit-animation: smokeup 2s linear infinite;
  -moz-animation: smokeup 2s linear infinite;
  -o-animation: smokeup 2s linear infinite;
  animation: smokeup 2s linear infinite;
}

.fume2 {
  width: 9px;
  height: 9px;
  margin-left: 38px;
  -webkit-animation: smokeup 2.1s linear infinite;
  -moz-animation: smokeup 2.1s linear infinite;
  -o-animation: smokeup 2.1s linear infinite;
  animation: smokeup 2.1s linear infinite;
}

.fume3 {
  width: 12px;
  height: 12px;
  margin-left: 48px;
  -webkit-animation: smokeup 2.2s linear infinite;
  -moz-animation: smokeup 2.2s linear infinite;
  -o-animation: smokeup 2.2s linear infinite;
  animation: smokeup 2.2s linear infinite;
}

.fume4 {
  width: 15px;
  height: 15px;
  margin-left: 38px;
  -webkit-animation: smokeup 2.3s linear infinite;
  -moz-animation: smokeup 2.3s linear infinite;
  -o-animation: smokeup 2.3s linear infinite;
  animation: smokeup 2.3s linear infinite;
}

.r-mountain {
  border-left: 110px rgba(0, 0, 0, 0) solid;
  border-right: 110px rgba(0, 0, 0, 0) solid;
  border-bottom: 170px #b3a385 solid;
  position: absolute;
  right: 80px;
  bottom: 40px;
}

.r-mountain:before {
  content: "";
  position: absolute;
  left: 0px;
  border-right: 110px rgba(0, 0, 0, 0) solid;
  border-bottom: 170px #a18e6e solid;
}

.r-mountain:after {
  content: "";
  position: absolute;
  left: -39px;
  border-left: 39px rgba(0, 0, 0, 0) solid;
  border-right: 39px rgba(0, 0, 0, 0) solid;
  border-bottom: 60px #fff solid;
}

.snow {
  margin: 0px;
  position: absolute;
  top: 9px;
  right: 153px;
}
.snow li {
  background: #fff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin: 0px;
  display: inline-block;
}

.gl:after {
  position: absolute;
  content: "";
  width: 50%;
  height: 40px;
  border-radius: 50px;
  background: #219065;
  bottom: 36px;
  left: 0px;
}

.left_m {
  left: 0px;
}

.solving {
  height: 100%;
  display: block;
}

@-webkit-keyframes smokeup {
  0% {
    margin-bottom: -3px;
    opacity: 1;
  }
  80% {
    margin-bottom: 7px;
    opacity: 0;
  }
  100% {
    margin-bottom: 7px;
    opacity: 0;
  }
}

@-moz-keyframes smokeup {
  0% {
    margin-bottom: -3px;
    opacity: 1;
  }
  80% {
    margin-bottom: 7px;
    opacity: 0;
  }
  100% {
    margin-bottom: 7px;
    opacity: 0;
  }
}

@-o-keyframes smokeup {
  0% {
    margin-bottom: -3px;
    opacity: 1;
  }
  80% {
    margin-bottom: 7px;
    opacity: 0;
  }
  100% {
    margin-bottom: 7px;
    opacity: 0;
  }
}

@keyframes smokeup {
  0% {
    margin-bottom: -3px;
    opacity: 1;
  }
  80% {
    margin-bottom: 7px;
    opacity: 0;
  }
  100% {
    margin-bottom: 7px;
    opacity: 0;
  }
}

@-webkit-keyframes planeanimate {
  0% {
    left: -10%;
  }
  100% {
    left: 110%;
  }
}

@-moz-keyframes planeanimate {
  0% {
    left: -10%;
  }
  100% {
    left: 110%;
  }
}

@-o-keyframes planeanimate {
  0% {
    left: -10%;
  }
  100% {
    left: 110%;
  }
}

@keyframes planeanimate {
  0% {
    left: -10%;
  }
  100% {
    left: 110%;
  }
}

@-webkit-keyframes boatanimate {
  0% {
    right: 250px;
  }
  100% {
    right: 1500px;
  }
}

@-moz-keyframes boatanimate {
  0% {
    right: 250px;
  }
  100% {
    right: 1500px;
  }
}

@-o-keyframes boatanimate {
  0% {
    right: 250px;
  }
  100% {
    right: 1500px;
  }
}

@keyframes boatanimate {
  0% {
    right: 250px;
  }
  100% {
    right: 1500px;
  }
}

@-webkit-keyframes night {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes night {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes night {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes night {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes bg-color-anim {
  0% {
    background-color: #85c6db;
  }
  10% {
    background-color: #85c6db;
  }
  40% {
    background-color: #0f355b;
  }
  60% {
    background-color: #0f355b;
  }
  90% {
    background-color: #85c6db;
  }
  100% {
    background-color: #85c6db;
  }
}

@media (min-width: 800px) and (max-width: 1024px) {
  .max_right {
    right: -80px;
  }
  .left_m {
    left: -80px;
  }
}

@media (max-width: 799px) {
  .cloud_left {
    top: 20%;
    left: 10%;
  }

  .cloud_right {
    top: 12%;
    left: 45%;
  }

  .sun {
    top: 12%;
    left: 45%;
  }

  .plane {
    top: 35%;
    left: 999px;
    -webkit-animation: planeanimate 5s linear infinite;
    -moz-animation: planeanimate 5s linear infinite;
    -o-animation: planeanimate 5s linear infinite;
    animation: planeanimate 5s linear infinite;
  }

  .body-start {
    height: 95%;
  }

  .landscape {
    bottom: 15px;
    width: 120px;
    height: 42px;
    z-index: 3;
  }

  .r-mountain {
    border-left: 55px rgba(0, 0, 0, 0) solid;
    border-right: 55px rgba(0, 0, 0, 0) solid;
    border-bottom: 85px #b3a385 solid;
    position: absolute;
    right: -45px;
    bottom: 40px;
  }

  .r-mountain:before {
    content: "";
    position: absolute;
    left: 0px;
    border-right: 55px rgba(0, 0, 0, 0) solid;
    border-bottom: 85px #a18e6e solid;
  }

  .r-mountain:after {
    content: "";
    position: absolute;
    left: -19px;
    border-left: 19px rgba(0, 0, 0, 0) solid;
    border-right: 19px rgba(0, 0, 0, 0) solid;
    border-bottom: 30px #fff solid;
  }

  .snow {
    display: none;
  }

  .boat {
    bottom: 15px;
    -webkit-animation: boatanimate 10s linear infinite;
    -moz-animation: boatanimate 10s linear infinite;
    -o-animation: boatanimate 10s linear infinite;
    animation: boatanimate 10s linear infinite;
  }

  @-webkit-keyframes boatanimate {
    0% {
      right: -10%;
    }
    100% {
      right: 120%;
    }
  }

  @-moz-keyframes boatanimate {
    0% {
      right: -10%;
    }
    100% {
      right: 120%;
    }
  }

  @-o-keyframes boatanimate {
    0% {
      right: -10%;
    }
    100% {
      right: 120%;
    }
  }

  @keyframes boatanimate {
    0% {
      right: -10%;
    }
    100% {
      right: 120%;
    }
  }
}

@media screen and (max-width: 799px) {
  .body-start {
    position: absolute;
    text-align: center;
    font-family: "Myriad Pro";
    color: white;
    display: block;
    width: 100%;
    height: 85vh;
    display: flex;
    z-index: -1;
  }
}
